@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;600;700&family=Inter:wght@400;500;600;700&display=swap');

:root {
  font-family: 'Inter', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  line-height: 1.4;
  font-weight: 400;
  color: #f6f0e8;
  background-color: #050505;
  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  min-height: 100vh;
  background-color: #050505;
  color: #f6f0e8;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  font: inherit;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

/* Auth Screens */
.login-container,
.register-container {
  display: flex;
  min-height: 100vh;
  background: #000000;
  color: #ffffff;
}

.login-left,
.register-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 80px 60px;
  background: #ffffff;
}

.register-left {
  overflow-y: auto;
}

.login-content,
.register-content {
  width: 100%;
  max-width: 420px;
}

.login-content h1,
.register-content h1 {
  margin: 0 0 8px 0;
  font-size: 36px;
  font-weight: 300;
  letter-spacing: -1px;
  color: #000000;
}

.subtitle {
  margin: 0 0 48px 0;
  font-size: 14px;
  color: #666666;
  font-weight: 400;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

.login-form,
.register-form {
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.register-form {
  gap: 24px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.form-group label {
  font-size: 12px;
  font-weight: 500;
  color: #000000;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

.form-group input {
  width: 100%;
  padding: 14px 0;
  border: none;
  border-bottom: 1px solid #e0e0e0;
  font-size: 16px;
  background: transparent;
  color: #000000;
  transition: border-color 0.3s ease;
  font-family: inherit;
}

.form-group input:focus {
  outline: none;
  border-bottom-color: #FFD700;
}

.form-group input::placeholder {
  color: #999999;
}

.password-input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.password-input-wrapper input {
  padding-right: 40px;
}

.password-toggle {
  position: absolute;
  right: 0;
  background: none;
  border: none;
  cursor: pointer;
  padding: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #666666;
  transition: color 0.2s;
}

.password-toggle:hover {
  color: #000000;
}

.password-toggle svg {
  width: 18px;
  height: 18px;
  stroke-width: 1.5;
}

.form-options {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 13px;
  margin-top: -8px;
}

.checkbox-label {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  color: #666666;
  user-select: none;
}

.checkbox-label input[type="checkbox"] {
  width: 16px;
  height: 16px;
  cursor: pointer;
  accent-color: #FFD700;
}

.forgot-password {
  color: #666666;
  text-decoration: none;
  font-weight: 400;
  transition: color 0.2s;
}

.forgot-password:hover {
  color: #000000;
}

.login-button,
.register-button {
  width: 100%;
  padding: 16px;
  background: #000000;
  color: #FFD700;
  border: 1px solid #000000;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-top: 16px;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-family: inherit;
}

.login-button:hover:not(:disabled),
.register-button:hover:not(:disabled) {
  background: #FFD700;
  color: #000000;
}

.login-button:active:not(:disabled),
.register-button:active:not(:disabled) {
  transform: scale(0.98);
}

.login-button:disabled,
.register-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.spinner {
  width: 18px;
  height: 18px;
}

.spinner circle {
  stroke: #FFD700;
}

.login-right,
.register-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 80px 60px;
  background: #000000;
  position: relative;
}

.brand-section {
  text-align: center;
  max-width: 400px;
}

.brand-logo {
  margin: 0 auto 32px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 24px 40px;
  border: 2px solid #FFD700;
  font-size: 48px;
  font-weight: 300;
  color: #FFD700;
  letter-spacing: 2px;
  min-width: 120px;
  min-height: 120px;
  box-sizing: border-box;
}

.brand-section h2 {
  margin: 0 0 16px 0;
  font-size: 32px;
  font-weight: 300;
  color: #ffffff;
  letter-spacing: 2px;
}

.brand-section p {
  margin: 0;
  font-size: 14px;
  color: #999999;
  font-weight: 300;
  letter-spacing: 1px;
  line-height: 1.6;
}

@media (min-width: 1200px) {
  .login-left,
  .login-right,
  .register-left,
  .register-right {
    padding: 100px 80px;
  }

  .login-content,
  .register-content {
    max-width: 480px;
  }
}

@media (max-width: 768px) {
  .register-container {
    flex-direction: column;
  }

  .register-right {
    display: none;
  }

  .register-left {
    padding: 40px 20px;
  }
}

/* Admin dashboard */
.admin-dashboard {
  position: relative;
  min-height: 100vh;
  padding: 32px clamp(20px, 4vw, 64px);
  background-image: linear-gradient(120deg, rgba(0, 0, 0, 0.85), rgba(10, 10, 10, 0.9)),
    url('https://images.unsplash.com/photo-1506377247377-2a5b3b417ebb?auto=format&fit=crop&w=1600&q=80');
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  overflow: hidden;
  color: #f6f0e8;
  font-family: 'Inter', system-ui, -apple-system, sans-serif;
}

.admin-dashboard::before {
  content: '';
  position: absolute;
  inset: 0;
  background: radial-gradient(circle at 30% 20%, rgba(255, 213, 128, 0.35), transparent 45%),
    radial-gradient(circle at 70% 80%, rgba(255, 255, 255, 0.15), transparent 60%);
  mix-blend-mode: screen;
  pointer-events: none;
}

.admin-shell {
  position: relative;
  z-index: 1;
  max-width: 1800px;
  margin: 0 auto;
}

.admin-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 24px;
  padding: 28px 36px;
  border-radius: 32px;
  border: 1px solid rgba(255, 255, 255, 0.15);
  background: rgba(5, 5, 5, 0.85);
  backdrop-filter: blur(18px);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.55);
  margin-bottom: 32px;
}

.admin-header-left h1 {
  margin: 0 0 8px 0;
  font-size: clamp(24px, 3vw, 36px);
  font-weight: 700;
  font-family: 'Playfair Display', 'Times New Roman', serif;
  color: #f5d26a;
  letter-spacing: 2px;
}

.admin-header-left .subtitle {
  margin: 8px 0 0;
  font-size: 12px;
  color: rgba(255, 255, 255, 0.65);
  letter-spacing: 6px;
  text-transform: uppercase;
}

.admin-header-left .greeting {
  margin: 16px 0 0;
  font-size: 15px;
  color: rgba(255, 255, 255, 0.85);
}

.admin-header-right {
  display: flex;
  flex-direction: column;
  gap: 12px;
  align-items: flex-end;
}

.admin-date-chip {
  padding: 6px 14px;
  background: rgba(10, 10, 10, 0.6);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 999px;
  font-size: 13px;
  color: rgba(255, 255, 255, 0.85);
}

.admin-btn {
  padding: 12px 32px;
  border-radius: 999px;
  background: linear-gradient(135deg, #f5d26a, #f1b953);
  color: #1a1a1a;
  font-weight: 600;
  letter-spacing: 0.5px;
  border: none;
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease, opacity 0.2s ease;
  box-shadow: 0 15px 35px rgba(244, 181, 67, 0.35);
}

.admin-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 25px 45px rgba(244, 181, 67, 0.5);
}

.admin-btn:disabled {
  opacity: 0.55;
  cursor: not-allowed;
  box-shadow: none;
}

.admin-btn.secondary {
  background: transparent;
  color: #f5d26a;
  border: 1px solid rgba(245, 210, 106, 0.6);
  box-shadow: none;
}

.admin-btn.secondary:hover:not(:disabled) {
  background: rgba(245, 210, 106, 0.1);
}

.admin-stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 24px;
  margin-bottom: 32px;
}

.admin-stat-card {
  padding: 28px;
  border-radius: 30px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(8, 8, 8, 0.9);
  backdrop-filter: blur(14px);
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.45);
  transition: all 0.3s ease;
}

.admin-stat-card:hover {
  transform: translateY(-4px);
  border-color: rgba(245, 210, 106, 0.4);
  box-shadow: 0 24px 60px rgba(244, 181, 67, 0.3);
  background: rgba(10, 10, 10, 0.95);
}

.admin-stat-label {
  margin: 0 0 12px 0;
  font-size: 13px;
  color: rgba(255, 255, 255, 0.7);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 600;
}

.admin-stat-value {
  margin: 0;
  font-size: 36px;
  font-weight: 700;
  color: #f5d26a;
  line-height: 1;
}

.admin-stat-value.small {
  font-size: 28px;
}

.admin-panel {
  background: rgba(10, 10, 10, 0.85);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 34px;
  padding: 36px;
  margin-bottom: 32px;
  backdrop-filter: blur(14px);
  box-shadow: 0 24px 60px rgba(0, 0, 0, 0.55);
}

.admin-panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  padding-bottom: 16px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);
}

.admin-panel-title {
  margin: 0;
  font-size: 26px;
  font-weight: 700;
  font-family: 'Playfair Display', 'Times New Roman', serif;
  color: #f6f0e8;
}

.admin-panel-subtitle {
  margin: 4px 0 0;
  font-size: 11px;
  color: rgba(255, 255, 255, 0.6);
  text-transform: uppercase;
  letter-spacing: 6px;
}

.admin-table-wrapper {
  overflow-x: auto;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.admin-table {
  width: 100%;
  border-collapse: collapse;
  background: rgba(10, 10, 10, 0.6);
}

.admin-table thead {
  background: rgba(245, 210, 106, 0.08);
}

.admin-table th {
  padding: 14px 36px;
  text-align: left;
  font-weight: 600;
  font-size: 11px;
  color: rgba(255, 255, 255, 0.6);
  text-transform: uppercase;
  letter-spacing: 3px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.admin-table td {
  padding: 14px 36px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  color: rgba(255, 255, 255, 0.85);
}

.admin-table tbody tr {
  transition: background-color 0.2s ease;
}

.admin-table tbody tr:hover {
  background: rgba(245, 210, 106, 0.08);
}

.admin-badge {
  display: inline-flex;
  align-items: center;
  padding: 6px 12px;
  border-radius: 999px;
  font-size: 12px;
  font-weight: 600;
  text-transform: capitalize;
  background: rgba(255, 255, 255, 0.12);
  color: #f6f0e8;
  border: 1px solid rgba(255, 255, 255, 0.18);
}

.admin-badge.admin {
  background: rgba(245, 210, 106, 0.2);
  color: #f5d26a;
  border: 1px solid rgba(245, 210, 106, 0.4);
}

.admin-badge.user {
  background: rgba(132, 196, 114, 0.18);
  color: #a5de8a;
  border: 1px solid rgba(132, 196, 114, 0.4);
}

.admin-alert {
  padding: 18px 26px;
  border-radius: 20px;
  margin-bottom: 32px;
  font-size: 14px;
  border: 1px solid rgba(255, 255, 255, 0.12);
  background: rgba(10, 10, 10, 0.75);
  backdrop-filter: blur(12px);
}

.admin-alert.error {
  border-color: rgba(255, 97, 97, 0.8);
  color: #ffb2b2;
}

.admin-alert.success {
  border-color: rgba(132, 196, 114, 0.7);
  color: #a5de8a;
}

.admin-placeholder {
  text-align: center;
  padding: 40px;
  color: rgba(255, 255, 255, 0.65);
  font-size: 14px;
}

.admin-loading {
  display: inline-block;
  width: 16px;
  height: 16px;
  border: 2px solid rgba(245, 210, 106, 0.3);
  border-top-color: #f5d26a;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 768px) {
  .admin-dashboard {
    padding: 20px 16px;
  }

  .admin-header {
    flex-direction: column;
    padding: 20px;
  }

  .admin-header-right {
    align-items: flex-start;
    width: 100%;
  }

  .admin-stats-grid {
    grid-template-columns: 1fr;
    gap: 16px;
  }

  .admin-panel {
    padding: 20px;
  }
}

/* User dashboard */
.ritzy-dashboard {
  position: relative;
  min-height: 100vh;
  padding: 56px clamp(24px, 5vw, 88px);
  background-image: linear-gradient(120deg, rgba(0, 0, 0, 0.85), rgba(10, 10, 10, 0.9)),
    url('https://images.unsplash.com/photo-1506377247377-2a5b3b417ebb?auto=format&fit=crop&w=1600&q=80');
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  overflow: hidden;
  color: #f6f0e8;
}

.ritzy-dashboard::before {
  content: '';
  position: absolute;
  inset: 0;
  background: radial-gradient(circle at 30% 20%, rgba(255, 213, 128, 0.35), transparent 45%),
    radial-gradient(circle at 70% 80%, rgba(255, 255, 255, 0.15), transparent 60%);
  mix-blend-mode: screen;
}

.ritzy-shell {
  position: relative;
  z-index: 1;
  max-width: 1600px;
  margin: 0 auto;
}

.ritzy-top-bar {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 24px;
  padding: 32px 40px;
  border-radius: 32px;
  border: 1px solid rgba(255, 255, 255, 0.15);
  background: rgba(5, 5, 5, 0.85);
  backdrop-filter: blur(18px);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.55);
  margin-bottom: 40px;
}

.ritzy-brand {
  font-family: 'Playfair Display', 'Times New Roman', serif;
  font-size: clamp(28px, 4vw, 40px);
  letter-spacing: 2px;
  margin: 0;
}

.ritzy-subtitle {
  margin: 8px 0 0;
  letter-spacing: 6px;
  text-transform: uppercase;
  font-size: 12px;
  color: rgba(255, 255, 255, 0.65);
}

.ritzy-greeting {
  margin: 16px 0 0;
  font-size: 15px;
  color: rgba(255, 255, 255, 0.85);
}

.ritzy-btn {
  padding: 12px 32px;
  border-radius: 999px;
  background: linear-gradient(135deg, #f5d26a, #f1b953);
  color: #1a1a1a;
  font-weight: 600;
  letter-spacing: 0.5px;
  border: none;
  transition: transform 0.2s ease, box-shadow 0.2s ease, opacity 0.2s ease;
  box-shadow: 0 15px 35px rgba(244, 181, 67, 0.35);
}

.ritzy-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 25px 45px rgba(244, 181, 67, 0.5);
}

.ritzy-btn:disabled {
  opacity: 0.55;
  cursor: not-allowed;
  box-shadow: none;
}

.ritzy-btn.ghost {
  background: transparent;
  color: #f5d26a;
  border: 1px solid rgba(245, 210, 106, 0.6);
  box-shadow: none;
}

.ritzy-btn.block {
  width: 100%;
  justify-content: center;
}

.ritzy-alert {
  margin-bottom: 32px;
  padding: 18px 26px;
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.12);
  background: rgba(10, 10, 10, 0.75);
  backdrop-filter: blur(12px);
  font-size: 14px;
}

.ritzy-alert.success {
  border-color: rgba(132, 196, 114, 0.7);
  color: #a5de8a;
}

.ritzy-alert.error {
  border-color: rgba(255, 97, 97, 0.8);
  color: #ffb2b2;
}

.ritzy-content-grid {
  display: grid;
  grid-template-columns: minmax(0, 2fr) minmax(320px, 1fr);
  gap: 40px;
  margin-bottom: 48px;
}

.ritzy-hero {
  position: relative;
  border-radius: 40px;
  padding: 64px 48px;
  text-align: center;
  background: radial-gradient(circle at top, rgba(255, 255, 255, 0.08), rgba(0, 0, 0, 0.85)),
    url('https://images.unsplash.com/photo-1500530855697-b586d89ba3ee?auto=format&fit=crop&w=900&q=80');
  background-size: cover;
  background-position: center;
  box-shadow: 0 40px 80px rgba(0, 0, 0, 0.5);
  overflow: hidden;
}

.ritzy-hero::after {
  content: '';
  position: absolute;
  inset: 0;
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 40px;
  mix-blend-mode: screen;
  pointer-events: none;
}

.ritzy-hero-kicker {
  letter-spacing: 6px;
  text-transform: uppercase;
  font-size: 12px;
  color: rgba(255, 255, 255, 0.75);
  margin-bottom: 16px;
}

.ritzy-hero-clock {
  font-family: 'Playfair Display', 'Times New Roman', serif;
  font-size: clamp(48px, 10vw, 96px);
  letter-spacing: 6px;
  text-shadow: 0 0 35px rgba(255, 255, 255, 0.5);
}

.ritzy-hero-caption {
  font-size: 13px;
  text-transform: uppercase;
  letter-spacing: 6px;
  color: rgba(255, 255, 255, 0.7);
  margin-top: 12px;
}

.ritzy-hero-meta {
  margin-top: 32px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  font-size: 14px;
  letter-spacing: 1px;
  color: rgba(255, 255, 255, 0.9);
}

.ritzy-panel {
  border-radius: 34px;
  padding: 36px;
  background: rgba(10, 10, 10, 0.85);
  border: 1px solid rgba(255, 255, 255, 0.08);
  box-shadow: 0 24px 60px rgba(0, 0, 0, 0.55);
  backdrop-filter: blur(14px);
}

.ritzy-overline {
  text-transform: uppercase;
  letter-spacing: 6px;
  font-size: 11px;
  color: rgba(255, 255, 255, 0.6);
  margin: 0 0 8px;
}

.ritzy-panel-title {
  font-family: 'Playfair Display', 'Times New Roman', serif;
  font-size: 26px;
  margin: 0;
}

.user-status-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 18px;
  margin-bottom: 24px;
}

.user-status-row {
  display: flex;
  flex-direction: column;
  gap: 6px;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.7);
}

.user-status-row strong {
  font-size: 18px;
  color: #f5d26a;
}

.ritzy-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 24px;
  margin-bottom: 48px;
}

.ritzy-action-card {
  border-radius: 30px;
  padding: 26px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(8, 8, 8, 0.9);
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.45);
}

.ritzy-action-card.disabled {
  opacity: 0.6;
}

.ritzy-action-card h3 {
  margin: 0 0 8px;
  font-family: 'Playfair Display', 'Times New Roman', serif;
}

.ritzy-action-card p {
  margin: 0 0 16px;
  color: rgba(255, 255, 255, 0.75);
  font-size: 14px;
}

.ritzy-history {
  margin-top: 48px;
  border-radius: 34px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  background: rgba(10, 10, 10, 0.92);
  box-shadow: 0 24px 60px rgba(0, 0, 0, 0.55);
}

.ritzy-history header {
  padding: 28px 36px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
  flex-wrap: wrap;
}

.ritzy-table-wrapper {
  width: 100%;
  overflow-x: auto;
}

.ritzy-history-table {
  width: 100%;
  border-collapse: collapse;
  min-width: 640px;
}

.ritzy-history-table th,
.ritzy-history-table td {
  padding: 14px 36px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  text-align: left;
}

.ritzy-history-table th {
  text-transform: uppercase;
  letter-spacing: 3px;
  font-size: 11px;
  color: rgba(255, 255, 255, 0.6);
}

.ritzy-placeholder {
  padding: 40px;
  text-align: center;
  color: rgba(255, 255, 255, 0.65);
}

.ritzy-badge {
  display: inline-flex;
  align-items: center;
  padding: 6px 12px;
  border-radius: 999px;
  font-size: 12px;
  text-transform: capitalize;
  font-weight: 600;
  background: rgba(255, 255, 255, 0.12);
  color: #f6f0e8;
  border: 1px solid rgba(255, 255, 255, 0.18);
}

.ritzy-badge.success,
.ritzy-badge.present {
  background: rgba(132, 196, 114, 0.18);
  color: #a5de8a;
  border-color: rgba(132, 196, 114, 0.4);
}

.ritzy-badge.pending {
  background: rgba(245, 210, 106, 0.2);
  color: #f5d26a;
  border-color: rgba(245, 210, 106, 0.4);
}

.ritzy-badge.error,
.ritzy-badge.absent {
  background: rgba(255, 122, 122, 0.2);
  color: #ffb2b2;
  border-color: rgba(255, 122, 122, 0.4);
}

.ritzy-calendar-chip {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 6px 14px;
  border-radius: 999px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  font-size: 13px;
}

@media (max-width: 1200px) {
  .ritzy-content-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  .ritzy-dashboard {
    padding: 32px 20px;
  }

  .ritzy-hero {
    padding: 48px 24px;
  }

  .ritzy-history-table {
    min-width: 580px;
  }
}


